class DailySpecials extends Component {
  constructor(props) {
    super(props);
    this.state = {
      // curItem: DailySpecialData[0]
      items: DailySpecialData,
      curIndex: 0
    };
    this.leftItemRef = React.createRef();
    this.rightItemRef = React.createRef();
  }
  componentDidMount() {
    const index = 0;
    this.rightItemRef.current.children[0].children[index].classList.add(
      'active'
    );
  }
  shouldComponentUpdate(nextProps, nextState) {
    // return this.state.curItem !== nextState.curItem;
    return this.state.curIndex !== nextState.curIndex;
  }
  setCurrentItem = (item, index) => {
    // this.setState({ curItem: item });
    this.setState({ curIndex: index });
    if (this.leftItemRef.current)
      this.leftItemRef.current.classList.add('animate');
    setTimeout(() => {
      if (this.leftItemRef.current)
        this.leftItemRef.current.classList.remove('animate');
    }, 1000);
    if (this.rightItemRef.current) {
      this.rightItemRef.current
        .querySelector('.active')
        .classList.remove('active');
      this.rightItemRef.current.children[0].children[index].classList.add(
        'active'
      );
    }
  };
  render() {
    // const { curItem } = this.state;
    // const leftStyle = {
    //   background: `url('${curItem.image}') center center / cover no-repeat`
    // };
    const { items, curIndex } = this.state;
    const leftStyle = {
      background: `url('${
        items[curIndex].image
      }') center center / cover no-repeat`
    };
    return (
      <div className="daily-specials">
        <h1 className="h1-heading-center mtb-10">Daily Special</h1>
        {/* <div
          style={{
            background: 'url("/images/rose-hr-260.jpg")',
            backgroundSize: 'cover',
            width: '200px',
            height: '24px',
            margin: '0 auto'
          }}
        /> */}
        <div className="rose-hr-box">
          <img
            className="rose-hr"
            src="/images/rose-hr-260.jpg"
            alt="rose hr"
          />
        </div>
        <div className="seoulContainer">
          <div className="right" ref={this.rightItemRef}>
            <div className="right-inner">
              {DailySpecialData.map((item, i) => (
                <DailySpecialRight
                  key={item.id}
                  currentItem={this.setCurrentItem}
                  item={item}
                  index={i}
                >
                  {item.day}
                </DailySpecialRight>
              ))}
            </div>
          </div>
          {this.state && items && (
            <div
              style={{ ...leftStyle }}
              className="left"
              ref={this.leftItemRef}
              onTransitionEnd={e => {
                console.log('ended');
              }}
              key={items[curIndex].id2}
            >
              <div className="content">
                <p className="left-day">{items[curIndex].day}</p>
                <p className="left-name">{items[curIndex].name}</p>
                <p className="left-desc">{items[curIndex].description}</p>
              </div>
            </div>
          )}
        </div>
      </div>
    );
  }
}

export default DailySpecials;

